/* Chat */

.user-chat-name {
  color:#C9082A;
  

}

.chat-box {
  width:300px;
  height:500px;
  margin: 0 auto;
  border: 1px solid;
  padding: 10px;
  box-shadow: 2px 10px 8px 10px #888888;
  background-image: url("https://images.pexels.com/photos/2291004/pexels-photo-2291004.jpeg?auto=compress&cs=tinysrgb&h=650&w=940");
  background-position: center;
  
}
.chatters {
margin-left:20px
}

.chat-container {
  border: 2px solid #dedede;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 10px;
  margin: 10px 0;
  opacity: 0.8;
  
  
}

.chat-container p {
  margin-top:13px;
  margin-left:-10px;
  
}

.darker {
  border-color: #ccc;
  background-color: #ddd;
  opacity: 0.8;

}

/* Clear floats */
.chat-container::after {
  content: "";
  clear: both;
  display: table;
}

/* Style images */
.chat-container img {
  float: left;
  max-width: 30px;
  width: 100%;
  margin-right: 20px;
  margin-top: 10px;
  border-radius: 50%;
}

.chat-container img.right {
  float: right;
  margin-left: 20px;
  margin-right:0;
}

.messages-container {
  padding:2px;
  overflow: auto;
  max-height:350px;
}

.chat-input {
  position:relative;
  bottom:0;
  margin-top:20px;
}

.other-chat-name{
  color:#17408B
}

.chat-header {
  color:white;
}

.chat-button{
  width:25%;
  height:30px;
}

.chatinput{
  border-radius:2%
}
/* CHARTS */

.radial-chart {
  position: relative;
  display: inline-block;
  transition: all 0.3s ease-in;
  
  
}
.radial-chart.no-progress .radial-chart-progress {
  opacity: 0;
}
.radial-chart-total {
  opacity: 0.3;
  

}
.radial-chart-progress {
  transform: rotate(90deg);
  transform-origin: center;
  transition: all 0.6s cubic-bezier(0.58, 0.16, 0.5, 1.14);
  transition-delay: 0.3s;
}

.chart-div {

margin-left:-5px;
margin-bottom:  -24px;
margin-top:3px

}

