  margin-top:120px;
}


/* Small devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .jumbotron h4 {
      font-size: 2em;
  }
  .home-container h5 {
    font-size: 1em;
  }
  .home-div h7 {
    font-size: .5em;
  }
  .home-cards {
    width:50%;
    margin:0 auto
  }
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .jumbotron h3 {
      font-size: 3em;
  }
  .home-container h5 {
    font-size: 2em;
  }
  .home-div h7 {
    font-size: 1em;
  }
  .bball-nav{
    width:50px;
    height:50px
  }
  
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .jumbotron h3 {
      font-size: 4em;
  }
  .home-container h5 {
    font-size: 3em;
  }
  .home-div h7 {
    font-size: 1.5em;
    
  }
  
  
}

